<template>
    <view
            :class="['s-col', classList]"
            :style="{ 'padding-left': padding, 'padding-right': padding }"
    >
        <slot></slot>
    </view>
</template>

<script>
    export default {
        name: "SunnyCol",
        props: {
            span: { type: [Number, String], default: 24 },
            offset: [Number, String],
            pull: [Number, String],
            push: [Number, String]
        },
        computed: {
            gutter() {
                let parent = this.$parent;
                while (parent && parent.$options.componentName !== "SunnyRow") {
                    parent = parent.$parent;
                }
                return parent ? parent.gutter : 0;
            },

            padding() {
                return this.gutter / 2 + "rpx";
            },

            classList() {
                let list = [];

                ["span", "offset", "pull", "push"].forEach(prop => {
                    let value = this[prop];

                    if (value || value === 0) {
                        list.push(
                            prop !== "span"
                                ? `s-col-${prop}-${value}`
                                : `s-col-${value}`
                        );
                    }
                });

                return list;
            }
        }
    };
</script>
<style lang="scss">
    .s-col {
        width: 100%;
        box-sizing: border-box;

    &::before {
         content: "";
         display: block;
     }
    }

    [class*="s-col-"] {
        float: left;
        box-sizing: border-box;
    }

    .s-col-0 {
        display: none;
        width: 0%;
    }

    @for $i from 1 through 24 {
        .s-col-push-#{$i},
        .s-col-pull-#{$i} {
            position: relative;
        }
    }

    @for $i from 1 through 24 {
        .s-col-#{$i} {
            width: 100% / 24 * $i;
        }

        .s-col-offset-#{$i} {
            margin-left: 100% / 24 * $i;
        }

        .s-col-pull-#{$i} {
            right: 100% / 24 * $i;
        }

        .s-col-push-#{$i} {
            left: 100% / 24 * $i;
        }
    }

</style>
